import { ref, computed } from 'vue'
import { defineStore } from 'pinia'
import axios from 'axios'
export const usePosts = defineStore({
  id:'posts',
  state:()=>({
    url:import.meta.env.VITE_URL,
    posts:null
  }),
  actions:{
   async getPosts(){
      const res = await axios.get(`${this.url}posts`)
      this.posts = res.data
    }
  },
  getters:{
  },
  persist: true
})
